"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"

interface ScheduleEntry {
  id: number;
  course_id: number;
  course_title: string;
  instructor_name: string;
  start_time: string;
  end_time: string;
  session_type: string;
  description: string;
  meeting_link?: string;
  location?: string;
}

interface Course {
  id: number;
  title: string;
  session_count: number;
}

interface ScheduleData {
  schedule: ScheduleEntry[];
  filters: {
    courses: Course[];
  };
  upcoming: ScheduleEntry[];
}

export default function SchedulePage() {
  const [data, setData] = useState<ScheduleData | null>(null)
  const [loading, setLoading] = useState(true)
  const [isAddingSession, setIsAddingSession] = useState(false)
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0])
  const [selectedCourse, setSelectedCourse] = useState("")

  const [sessionForm, setSessionForm] = useState({
    course_id: "",
    start_time: "",
    end_time: "",
    session_type: "lecture",
    description: "",
    meeting_link: "",
    location: ""
  })

  const fetchSchedule = async () => {
    try {
      const params = new URLSearchParams()
      if (selectedDate) params.append('start', selectedDate)
      if (selectedCourse) params.append('course', selectedCourse)

      const response = await fetch(`/api/schedule?${params}`)
      if (!response.ok) {
        throw new Error('Failed to fetch schedule')
      }
      const data = await response.json()
      setData(data)
    } catch (error) {
      console.error('Error fetching schedule:', error)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchSchedule()
  }, [selectedDate, selectedCourse])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      const response = await fetch('/api/schedule', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(sessionForm),
      })

      if (!response.ok) {
        throw new Error('Failed to create session')
      }

      fetchSchedule()
      setIsAddingSession(false)
      setSessionForm({
        course_id: "",
        start_time: "",
        end_time: "",
        session_type: "lecture",
        description: "",
        meeting_link: "",
        location: ""
      })
    } catch (error) {
      console.error('Error creating session:', error)
    }
  }

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-1/3 mb-8"></div>
          <div className="space-y-6">
            {[1, 2, 3].map((i) => (
              <Card key={i} className="p-6">
                <div className="space-y-4">
                  <div className="h-6 bg-gray-200 rounded w-1/3"></div>
                  <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                </div>
              </Card>
            ))}
          </div>
        </div>
      </div>
    )
  }

  if (!data) {
    return (
      <div className="p-8">
        <Card className="p-12 text-center">
          <p className="text-lg text-gray-500">Failed to load schedule</p>
        </Card>
      </div>
    )
  }

  const groupedSchedule = data.schedule.reduce((acc, session) => {
    const date = new Date(session.start_time).toLocaleDateString()
    if (!acc[date]) acc[date] = []
    acc[date].push(session)
    return acc
  }, {} as Record<string, ScheduleEntry[]>)

  return (
    <div className="p-8">
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-2xl font-bold mb-2">Course Schedule</h1>
          <p className="text-gray-600">View and manage your course sessions</p>
        </div>
        <Button onClick={() => setIsAddingSession(true)}>Add Session</Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        <div className="lg:col-span-2">
          <div className="flex gap-4 mb-6">
            <Input
              type="date"
              value={selectedDate}
              onChange={(e) => setSelectedDate(e.target.value)}
              className="w-48"
            />
            <select
              className="px-4 py-2 border rounded-lg bg-white"
              value={selectedCourse}
              onChange={(e) => setSelectedCourse(e.target.value)}
            >
              <option value="">All Courses</option>
              {data.filters.courses.map((course) => (
                <option key={course.id} value={course.id}>
                  {course.title} ({course.session_count} sessions)
                </option>
              ))}
            </select>
          </div>

          {Object.entries(groupedSchedule).map(([date, sessions]) => (
            <div key={date} className="mb-8">
              <h2 className="text-lg font-semibold mb-4">{date}</h2>
              <div className="space-y-4">
                {sessions.map((session) => (
                  <Card key={session.id} className="p-6">
                    <div className="flex justify-between items-start">
                      <div>
                        <h3 className="font-semibold">{session.course_title}</h3>
                        <p className="text-sm text-gray-500">
                          {session.instructor_name} â€¢ {session.session_type}
                        </p>
                      </div>
                      <div className="text-right">
                        <p className="font-medium">
                          {new Date(session.start_time).toLocaleTimeString()} - 
                          {new Date(session.end_time).toLocaleTimeString()}
                        </p>
                        {session.location && (
                          <p className="text-sm text-gray-500">{session.location}</p>
                        )}
                      </div>
                    </div>

                    <p className="mt-4 text-gray-600">{session.description}</p>

                    {session.meeting_link && (
                      <div className="mt-4">
                        <Button variant="outline" asChild>
                          <a href={session.meeting_link} target="_blank" rel="noopener noreferrer">
                            Join Meeting
                          </a>
                        </Button>
                      </div>
                    )}
                  </Card>
                ))}
              </div>
            </div>
          ))}

          {Object.keys(groupedSchedule).length === 0 && (
            <Card className="p-12 text-center">
              <p className="text-lg text-gray-500">No sessions scheduled</p>
            </Card>
          )}
        </div>

        <div>
          <Card className="p-6 mb-6">
            <h2 className="text-lg font-semibold mb-4">Upcoming Sessions</h2>
            <div className="space-y-4">
              {data.upcoming.map((session) => (
                <div key={session.id} className="p-4 bg-gray-50 rounded-lg">
                  <p className="font-medium">{session.course_title}</p>
                  <p className="text-sm text-gray-500">
                    {new Date(session.start_time).toLocaleDateString()}{" "}
                    {new Date(session.start_time).toLocaleTimeString()}
                  </p>
                  <p className="text-sm text-gray-600 mt-2">{session.description}</p>
                </div>
              ))}

              {data.upcoming.length === 0 && (
                <p className="text-gray-500 text-center">No upcoming sessions</p>
              )}
            </div>
          </Card>
        </div>
      </div>

      {/* Add Session Form */}
      {isAddingSession && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
          <Card className="w-full max-w-2xl p-6">
            <h2 className="text-xl font-bold mb-6">Add New Session</h2>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid grid-cols-2 gap-6">
                <div className="space-y-2">
                  <Label htmlFor="course">Course</Label>
                  <select
                    id="course"
                    className="w-full px-4 py-2 border rounded-lg"
                    value={sessionForm.course_id}
                    onChange={(e) => setSessionForm(prev => ({ ...prev, course_id: e.target.value }))}
                    required
                  >
                    <option value="">Select Course</option>
                    {data.filters.courses.map((course) => (
                      <option key={course.id} value={course.id}>
                        {course.title}
                      </option>
                    ))}
                  </select>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="type">Session Type</Label>
                  <select
                    id="type"
                    className="w-full px-4 py-2 border rounded-lg"
                    value={sessionForm.session_type}
                    onChange={(e) => setSessionForm(prev => ({ ...prev, session_type: e.target.value }))}
                    required
                  >
                    <option value="lecture">Lecture</option>
                    <option value="workshop">Workshop</option>
                    <option value="discussion">Discussion</option>
                    <option value="review">Review</option>
                  </select>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="start">Start Time</Label>
                  <Input
                    id="start"
                    type="datetime-local"
                    value={sessionForm.start_time}
                    onChange={(e) => setSessionForm(prev => ({ ...prev, start_time: e.target.value }))}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="end">End Time</Label>
                  <Input
                    id="end"
                    type="datetime-local"
                    value={sessionForm.end_time}
                    onChange={(e) => setSessionForm(prev => ({ ...prev, end_time: e.target.value }))}
                    required
                  />
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="description">Description</Label>
                <Textarea
                  id="description"
                  value={sessionForm.description}
                  onChange={(e) => setSessionForm(prev => ({ ...prev, description: e.target.value }))}
                  required
                />
              </div>

              <div className="grid grid-cols-2 gap-6">
                <div className="space-y-2">
                  <Label htmlFor="meeting">Meeting Link</Label>
                  <Input
                    id="meeting"
                    type="url"
                    value={sessionForm.meeting_link}
                    onChange={(e) => setSessionForm(prev => ({ ...prev, meeting_link: e.target.value }))}
                    placeholder="https://..."
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="location">Location</Label>
                  <Input
                    id="location"
                    value={sessionForm.location}
                    onChange={(e) => setSessionForm(prev => ({ ...prev, location: e.target.value }))}
                    placeholder="Room number or building"
                  />
                </div>
              </div>

              <div className="flex gap-4">
                <Button type="submit">Add Session</Button>
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => setIsAddingSession(false)}
                >
                  Cancel
                </Button>
              </div>
            </form>
          </Card>
        </div>
      )}
    </div>
  )
}
